import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Subject, Observable, BehaviorSubject } from 'rxjs';
import { v4 as uuidv4 } from 'uuid';
import { ColorScheme, Message, MessageHandlers, MessageType, Payload, User } from './message.model';
import { AvlAuthService } from '@avl-ng-controls/auth';

@Injectable({
  providedIn: 'root'
})
export class ShellCommunicationService {
  private readonly id: string;

  private handlers: MessageHandlers = {
    [MessageType.theme]: (theme: string) => this.handleTheme(theme),
    [MessageType.locale]: (locale: string) => this.handleLocale(locale),
    [MessageType.language]: (language: string) => this.handleLanguage(language),
    [MessageType.colorScheme]: (colorScheme: ColorScheme) => this.handleColorScheme(colorScheme),
    [MessageType.navigation]: (url: string) => this.handleNavigation(url),
    [MessageType.action]: (action: string) => this.handleAction(action),
    [MessageType.authentication]: (user: User) => this.handleAuthentication(user),
    [MessageType.redirectedFromLogIn]: (redirected: boolean) => this.handleRedirectedFromLogIn(redirected),
  };

  private themeSubject = new Subject<string>();
  private languageSubject = new Subject<string>();
  private actionSubject = new Subject<string>();
  private localeSubject = new Subject<string>();
  private colorSchemeSubject = new Subject<ColorScheme>();

  /**
  * Observable tracking theme
  * Emits "dark" if dark theme selected, "light" otherwise 
  */
  public theme$: Observable<string> = this.themeSubject;

  /**
  * Observable tracking language
  */
  public language$: Observable<string> = this.languageSubject;
  public action$: Observable<string> = this.actionSubject;

  /**
  * Observable tracking country changes
  */
  public locale$: Observable<string> = this.localeSubject;

  /**
  * Observable tracking color scheme changes
  */
  public colorScheme$: Observable<ColorScheme> = this.colorSchemeSubject;

  /**
  * Observable tracking login state
  * Resolves to true on successful login, false otherwise
  */
  public isLoggedIn$: Observable<boolean>;

  /**
   * Observable tracking if redirected from idb
   * Resolves to true on redirection, false otherwise
   */
  public redirectedFromLogIn = new BehaviorSubject<boolean>(undefined);
  
  /**
   * If current application is inside shell application 
   * True if current application is inside shell application, false otherwise.
   */
  public isChild = window.location !== window.parent.location;
  
  constructor(private router: Router, private authService: AvlAuthService) {
    window.addEventListener('message', (event) => this.onMessage(event));
    
    this.id = uuidv4();
  }
  
  public notifyShellAboutAction(action: string): void {
    this.postMessageToShell(MessageType.action, action);
  }
  
  /**
   * Sends message to shell that it needs to navigate to login page
   */
  public notifyShellAboutLogin(url: string): void {
    this.postMessageToShell(MessageType.login, url);
  }
  
  /**
   * Sends message to shell that it needs to navigate to @url
   */
  public notifyShellAboutNavigation(url: string): void {
    this.postMessageToShell(MessageType.navigation, url);
  }
  
  /**
   * Sends registration message to shell 
   */
  public registerToShell(): void {
    this.postMessageToShell(MessageType.registration, this.id);
  }

  private onMessage(event: MessageEvent): void {
    const { payload, messageType }: Message = event.data;
    const handlers = {
      ...this.handlers,
      default: () => console.log('Unrecognized message for the shell.', event)
    };

    (handlers[messageType] || handlers.default)(payload);
  }

  private postMessageToShell(messageType: MessageType, payload: Payload): void {
    const message: Message = { messageType, payload };
    window.parent.window.postMessage(message, '*');
  }

  private handleColorScheme(colorScheme: ColorScheme): void {
    this.colorSchemeSubject.next(colorScheme);
  }

  private handleLanguage(language: string): void {
    this.languageSubject.next(language);
  }

  private handleLocale(locale: string): void {
    this.localeSubject.next(locale);
  }

  private handleTheme(theme: string): void {
    this.themeSubject.next(theme);
  }

  private handleNavigation(url: string): void {
    this.router.navigateByUrl(url);
  }

  private handleAction(action: string): void {
    this.actionSubject.next(action);
  }

  private handleAuthentication(user: User): void {
    if (user?.access_token) {
      this.setUserToSessionStorage(user);
    } else {
      this.removeUserFromSessionStorage();
    }

    this.authService.reloadPersistedUserData();
  }

  private handleRedirectedFromLogIn(redirected: boolean) {
    this.redirectedFromLogIn.next(redirected);
  }

  private setUserToSessionStorage(user: User): void {
    sessionStorage.setItem('refresh_token', user.refresh_token);
    sessionStorage.setItem('access_token', user.access_token);
    sessionStorage.setItem('id_token', user.id_token);
    sessionStorage.setItem('refresh_token_expires_at', String(user.refresh_token_expires_at));
    sessionStorage.setItem('access_token_expires_at', String(user.access_token_expires_at));
    sessionStorage.setItem('access_token_stored_at', String(user.access_token_stored_at));
    sessionStorage.setItem('username', user.username);
    sessionStorage.setItem('name', user.name);
    sessionStorage.setItem('roles', JSON.stringify(user.roles));
  }

  private removeUserFromSessionStorage(): void {
    sessionStorage.removeItem('refresh_token');
    sessionStorage.removeItem('access_token');
    sessionStorage.removeItem('id_token');
    sessionStorage.removeItem('refresh_token_expires_at');
    sessionStorage.removeItem('access_token_expires_at');
    sessionStorage.removeItem('access_token_stored_at');
    sessionStorage.removeItem('username');
    sessionStorage.removeItem('name');
    sessionStorage.removeItem('roles');
  }
}
