import { ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { LogConfig } from './logging.model';
import { LoggingService } from './logging.service';

@NgModule()
export class LoggingModule {
  constructor(@Optional() @SkipSelf() parentModule?: LoggingModule) {
    if (parentModule) {
      throw new Error('LoggingModule is already loaded. Import it in the AppModule only');
    }
  }
  static forRoot(config: LogConfig): ModuleWithProviders<LoggingModule> {
    return {
      ngModule: LoggingModule,
      providers: [LoggingService, { provide: 'loggingConfig', useValue: config }],
    };
  }
}
