export interface User {
  username: string;
  name?: string;

  access_token?: string;
  access_token_expires_at?: number;
  access_token_stored_at?: number;

  refresh_token?: string;
  refresh_token_expires_at?: number;

  id_token?: string;
  id_token_expires_at?: number;

  roles?: string[];
}

export enum MessageType {
  action = 'action',
  colorScheme = 'colorScheme',
  language = 'language',
  locale = 'locale',
  navigation = 'navigation',
  registration = 'registration',
  theme = 'theme',
  login = 'login',
  authentication = 'authentication',
  redirectedFromLogIn = 'redirectedFromLogIn'
}

export interface Message {
  messageType: MessageType;
  payload: Payload;
}

export type Payload = string | ColorScheme | User | boolean;

export interface MessageHandlers {
  [key: string]: (payload: Payload) => void;
}
export interface ColorScheme {
  '--avl-product-color': string;
  '--avl-product-hover-color': string;
  '--avl-product-focus-color': string;
}
