import { Injectable } from "@angular/core";
import { TranslationManagementService } from "./translation-management.service";
import { ThemeService } from "./theme.service";
import { Subject, Observable } from "rxjs";
import {
  ColorScheme,
  Message,
  MessageHandlers,
  MessageType,
  Payload,
  User,
} from "./message.model";

@Injectable({
  providedIn: "root",
})
export class ChildCommunicationService {
  private handlers: MessageHandlers = {
    [MessageType.login]: (url: string) => this.handleLogin(url),
    [MessageType.navigation]: (url: string) => this.handleNavigation(url),
    [MessageType.action]: (action: string) => this.handleAction(action),
    [MessageType.registration]: (_) => this.handleRegistration(),
  };

  private actionSubject = new Subject<string>();
  private navigationSubject = new Subject<string>();
  private loginSubject = new Subject<string>();
  private registrationCompletedSubject = new Subject<boolean>();

  protected iframeEl: HTMLIFrameElement;

  public action$: Observable<string> = this.actionSubject;

  /**
   * Observable tracking url to navigate to
   */
  public navigation$: Observable<string> = this.navigationSubject;

  /**
   * Observable tracking url for login
   * Url for login provided by child application
   */
  public login$: Observable<string> = this.loginSubject;

  /**
   * Observable tracking child registration
   * Emits true if registration was successful, false otherwise
   */
  public registrationCompleted$: Observable<boolean> = this
    .registrationCompletedSubject;

  constructor(
    private translationManagementService: TranslationManagementService,
    private themeService: ThemeService
  ) {
    window.addEventListener("message", (event) => this.onMessage(event));
    this.themeService.themeChanged.subscribe(() => this.postThemeToChildren());
  }

  /**
   * Sends message to child about locale
   */
  public notifyChildAboutLocale(country: string): void {
    this.postMessageToChild(MessageType.locale, country);
  }

  /**
   * Sends message to child about language
   */
  public notifyChildAboutLanguage(language: string): void {
    this.postMessageToChild(MessageType.language, language);
  }

  /**
   * Sends message to child about action
   */
  public notifyChildAboutAction(action: string): void {
    this.postMessageToChild(MessageType.action, action);
  }

  /**
   * Sends message to child about navigation
   */
  public notifyChildAboutNavigation(url: string): void {
    this.postMessageToChild(MessageType.navigation, url);
  }

  /**
   * Sends messages to children about current user
   */
  public postAuthenticationDataToChildren() {
    const payload: User = {
      refresh_token: sessionStorage.getItem("refresh_token"),
      access_token: sessionStorage.getItem("access_token"),
      id_token: sessionStorage.getItem("id_token"),
      refresh_token_expires_at: Number(
        sessionStorage.getItem("refresh_token_expires_at")
      ),
      access_token_expires_at: Number(
        sessionStorage.getItem("access_token_expires_at")
      ),
      access_token_stored_at: Number(
        sessionStorage.getItem("access_token_stored_at")
      ),
      username: sessionStorage.getItem("username"),
      name: sessionStorage.getItem("name"),
      roles: JSON.parse(sessionStorage.getItem("roles")),
    };

    this.postMessageToChild(MessageType.authentication, payload);
  }

  /**
   * Sends messages to children about selected application theme
   */
  public postThemeToChildren(): void {
    const theme = this.themeService.getCurrentTheme();
    this.postMessageToChild(MessageType.theme, theme);
  }

  /**
   * Sends messages to children about selected language
   */
  public postLanguageToChildren(): void {
    this.postMessageToChild(
      MessageType.language,
      this.translationManagementService.language
    );
  }

  /**
   * Sends messages to children about selected country
   */
  public postLocaleToChildren(): void {
    const country = localStorage.getItem("country");
    this.postMessageToChild(MessageType.locale, country);
  }

  /**
   * Sends message to child about redirection from idb
   */
  public postRedirectedFromLogIn(payload: boolean): void {
    this.postMessageToChild(MessageType.redirectedFromLogIn, payload);
  }

  /**
   * Sends message to children about color scheme
   */
  public postColorSchemeToChildren(): void {
    const computedStyle = window.getComputedStyle(document.body);
    const productColor = computedStyle.getPropertyValue("--avl-product-color");
    const productHoverColor = computedStyle.getPropertyValue(
      "--avl-product-hover-color"
    );
    const productFocusColor = computedStyle.getPropertyValue(
      "--avl-product-focus-color"
    );

    const colorScheme: ColorScheme = {
      "--avl-product-color": productColor,
      "--avl-product-hover-color": productHoverColor,
      "--avl-product-focus-color": productFocusColor,
    };

    this.postMessageToChild(MessageType.colorScheme, colorScheme);
  }

  private handleAction(action: string): void {
    this.actionSubject.next(action);
  }

  private handleNavigation(navigation: string): void {
    this.navigationSubject.next(navigation);
  }

  private handleLogin(login: string): void {
    this.loginSubject.next(login);
  }

  // when child started its registration iframe is loaded and shell gets a message that he can send messages to iframe
  private handleRegistration(): void {
    this.iframeEl = document.getElementsByTagName(
      "iframe"
    )[0] as HTMLIFrameElement;

    this.postThemeToChildren();
    this.postColorSchemeToChildren();
    this.postLanguageToChildren();
    this.postLocaleToChildren();

    this.registrationCompletedSubject.next(true);
  }

  private onMessage(event: MessageEvent): void {
    const { payload, messageType }: Message = event.data;
    const handlers = {
      ...this.handlers,
      default: () => console.log("Unrecognized message for the child.", event),
    };

    (handlers[messageType] || handlers.default)(payload);
  }

  private postMessageToChild(messageType: MessageType, payload: Payload): void {
    if (!this.iframeEl) {
      return;
    }

    const message: Message = { messageType, payload };
    this.iframeEl.contentWindow?.postMessage(message, "*");
  }
}
